import React from 'react';

const Page = ({onButtonLogOut}) => {
	return (
		<div className='flex flex-column vh-100 bg-light-blue'>
				<button 
				onClick={onButtonLogOut}
				className='f7 w-20 ml7 mr0 mb2 mt2 dim br3 bg-light-red shadow-5'>
					Log out
				</button>
			<div className='db center w-80'>
				<div className='f6 center dim ph3 pv2 mb2 tc white bg-green w-65 br3 shadow-5'>1. Propisi u cestovnom prometu</div>
				<div className='f6 center dim ph3 pv2 mb2 tc white bg-green w-65 br3 shadow-5'>2. Ponašanje sudionika u prometu</div>
				<div className='f6 center dim ph3 pv2 mb2 tc white bg-green w-65 br3 shadow-5'>3. Cesta i njena obilježja</div>
				<div className='f6 center dim ph3 pv2 mb2 tc white bg-green w-65 br3 shadow-5'>4. Znakovi u prometu</div>
				<div className='f6 center dim ph3 pv2 mb2 tc white bg-green w-65 br3 shadow-5'>5. Ostali sudionici u cestovnom prometu</div>
				<div className='f6 center dim ph3 pv2 mb2 tc white bg-green w-65 br3 shadow-5'>6. Uočavanje prometnih uvjeta i situacije</div>
				<div className='f6 center dim ph3 pv2 mb2 tc white bg-green w-65 br3 shadow-5'>7. Prometna pravila</div>
				<div className='f6 center dim ph3 pv2 mb2 tc white bg-green w-65 br3 shadow-5'>8. Vozilo u funkciji sigurnosti prometa</div>
				<div className='f6 center dim ph3 pv2 mb2 tc white bg-green w-65 br3 shadow-5'>9. / 10. Vožnja u naselju / Vožnja izvan naselja</div>
				<div className='f6 center dim ph3 pv2 mb2 tc white bg-green w-65 br3 shadow-5'>11. Postupak u slučaju prometne nesreće</div>
				<div className='f6 center dim ph3 pv2 mb2 tc white bg-green w-65 br3 shadow-5'>12. Vožnja u posebnim uvjetima</div>
			</div>
		</div>
	);
}

export default Page;