import React from 'react';
import autoskolaL from './autoskolaL.svg';

const Login = ({onInputChange, onButtonPristupi}) => { 
    return (
      <div className="App">
        <header className="App-header">
          <img src={autoskolaL} className="App-logo" alt="logo" />
          <p>
            Prometni propisi i sigurnosna pravila
          </p>
          <input
          type='password'
          name='password'
          className='bg-light-blue w-30 ma2' 
          placeholder='Unesi lozinku'
          onChange={onInputChange}
          />
            <button
            onClick={onButtonPristupi}
            className="f5 bg-green br-pill grow ba bw2 ph3 pv1">
              Pristupi
            </button>
        </header>
      </div>
    );
}

export default Login;
